<template>
<div>
  <el-row>
    <el-col :span="12" :offset="6">
      <el-form ref="form" :model="form">
        <el-form-item label="属性抽取">
          <el-input autosize type="textarea" v-model="form.UserInput"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button class="bg" type="primary" @click="attr_extract">抽取</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
  <el-row>
    <el-col :span="12" :offset="6">
      <ResultList :ResultListData="ResultListData"/>
    </el-col>
  </el-row>
</div>
</template>

<script>
/* eslint-disable */
import axios from "axios";
import ResultList from "./ResultList";
const EXTRACT_ENDPOINT = "http://139.199.122.223:1234/";

export default {
  name: "HelloWorld",
  components: {
    ResultList
  },
  data() {
    return {
      form: {
        UserInput: `在演出开始前，文在寅与朝鲜高级别代表团在休息室进行了简短交谈。朝鲜代表团团长、朝鲜最高人民会议常任委员会委员长金永南表示，在此次访问中，代表团和文在寅总统交换了意见，为北南双方经常见面创造了机会。文在寅则表示，这次的会面非常珍贵，希望南北齐心协力，让这次会面播下的火种变为熊熊燃烧的火炬。`
      },
      ResultListData: []
    };
  },
  methods: {
    attr_extract: async function(event) {
      try {
        const attr__list = (await axios.post(EXTRACT_ENDPOINT, {
          UserInput: encodeURI(this.form.UserInput)
        })).data.person_with_gender__list;
        console.log(attr__list)
        this.ResultListData = attr__list;
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
  color: #42b983;
}

.bg {
  width: 100%;
}
</style>



